/* Style for the container that holds the sidebar and content */
.page-content {
    display: flex; /* Use flexbox to create a sidebar layout */
    flex-direction: row; /* Make the sidebar and content side-by-side */
    justify-content: center;
    margin: 0;
}

@media (max-width: 768px) {
    /* Define your mobile-specific styles here */
    /* For example, you can hide specific elements */
    .hide-on-mobile {
      display: none;
    }
  }

@media (min-width: 0px) {
    /* Define your mobile-specific styles here */
    /* For example, you can hide specific elements */
    .hide-on-website {
      color: white;
      justify-content: center;
      align-items: center;
      text-align: center;
      margin-top: 50%;
    }
  }

@media (min-width: 769px) {
    .hide-on-website {
      display: none;
    }
  }

.under-navbar {
    display: flex; /* Use flexbox to create a sidebar layout */
    flex-direction: row; /* Make the sidebar and content side-by-side */
    justify-content: center;
